/* NGN Chassis PostCSS

/* Color Definitions ---------------------------------------------------------*/

:root {
	--white : rgba(255,255,255,1);
	--black : rgba(0,0,0,1);

	--red   : red;
	--green : rgba(104,187,18,1);
	--blue  : rgba(0,152,230,1);

	/* Copic Greys
	 * Grey Scale: lightest (00) to darkest (10) in one-shade increments */

	/* :: Cool Grey */
	--grey-c00 : rgba(232,240,243,1); /* #EDEDED */
	--grey-c01 : rgba(225,233,236,1); /* #EAEAEA */
	--grey-c02 : rgba(218,227,232,1); /* #E1E1DF */
	--grey-c03 : rgba(204,215,221,1); /* #D3D4CF */
	--grey-c04 : rgba(192,203,209,1); /* #BCBBB9 */
	--grey-c05 : rgba(146,160,169,1); /* #A8A7A3 */
	--grey-c06 : rgba(125,139,150,1); /* #939391 */
	--grey-c07 : rgba(99,112,121,1);  /* #777674 */
	--grey-c08 : rgba(83,93,103,1);   /* #636361 */
	--grey-c09 : rgba(60,71,77,1);    /* #4c4b49 */
	--grey-c10 : rgba(33,42,49,1);    /* #332F2E */

	/* :: Neutral Grey */
	--grey-n00 : rgba(237,237,237,1); /* #EEEEEE */
	--grey-n01 : rgba(226,227,229,1); /* #E2E3E5 */
	--grey-n02 : rgba(218,219,221,1); /* #DADBDD */
	--grey-n03 : rgba(209,210,212,1); /* #D1D2D4 */
	--grey-n04 : rgba(188,189,193,1); /* #BCBDC1 */
	--grey-n05 : rgba(169,170,174,1); /* #A9AAAE */
	--grey-n06 : rgba(148,149,153,1); /* #949599 */
	--grey-n07 : rgba(119,120,124,1); /* #77787C */
	--grey-n08 : rgba(99,100,102,1);  /* #636466 */
	--grey-n09 : rgba(76,77,79,1);    /* #4C4D4F */
	--grey-n10 : rgba(44,45,48,1);    /* #312F32 */

	/* :: Toner Grey */
	--grey-t00 : rgba(237,237,237,1); /* #E8F0F3 */
	--grey-t01 : rgba(234,234,234,1); /* #E1E9EC */
	--grey-t02 : rgba(225,225,223,1); /* #DAE3E8 */
	--grey-t03 : rgba(211,212,207,1); /* #CCD7DD */
	--grey-t04 : rgba(188,187,185,1); /* #C0CBD1 */
	--grey-t05 : rgba(168,167,163,1); /* #92A0A9 */
	--grey-t06 : rgba(147,147,145,1); /* #7D8B96 */
	--grey-t07 : rgba(117,118,119,1); /* #637079 */
	--grey-t08 : rgba(99,99,97,1);    /* #535D67 */
	--grey-t09 : rgba(76,75,73,1);    /* #3C474D */
	--grey-t10 : rgba(51,47,46,1);    /* #212A31 */

}

